/* === LOGIN / REGISTER CONTAINER === */
.login-container,
.register-container {
  width: 320px;
  margin: 100px auto;
  text-align: center;
  border: 1px solid #ddd;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.1);
}

.login-container input,
.register-container input {
  width: 90%;
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 6px;
  border: 1px solid #ccc;
}

/* === BOTONES DE LOGIN / REGISTER === */
.login-container button,
.register-container button {
  width: 95%;
  padding: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.login-container button:hover,
.register-container button:hover {
  background-color: #0056b3;
}

/* === LINKS DEL LOGIN / REGISTER === */
.login-actions {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-top: 15px;
}

.login-link {
  text-decoration: none;
  color: #007bff;
  font-size: 14px;
  text-align: center;
  transition: 0.2s;
}

.login-link:hover {
  color: #0056b3;
  text-decoration: underline;
}

.forgot-link {
  margin-top: 10px;
  text-align: center;
  font-size: 13px;
  color: #666;
}

/* === ERRORES === */
.error {
  color: red;
  font-size: 14px;
}

.login-message {
  margin-top: 12px;
  text-align: center;
  font-size: 14px;
  color: #00ff2f;
}