/* === TOP BAR === */
.top-bar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #007bff;
  color: white;
  display: flex;
  flex-wrap: wrap; /* para que los elementos se muevan en móviles */
  align-items: center;
  justify-content: space-between;
  padding: 10px 15px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.2);
  z-index: 1000;
  gap: 10px;
  box-sizing: border-box;
}

.top-bar .welcome,
.top-bar .UserToken {
  font-size: 14px;
  margin-right: 10px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.top-bar .UserToken {
  font-size: 12px;
  opacity: 0.8;
}

/* === BOTÓN DE CERRAR SESIÓN === */
.top-bar form button,
.logout-button {
  padding: 8px 16px;
  background-color: #dc3545; /* rojo */
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.3s;
}

.top-bar form button:hover,
.logout-button:hover {
  background-color: #c82333;
}

/* === RESPONSIVE TOP BAR === */
@media (max-width: 480px) {
  .top-bar {
    flex-direction: column;
    align-items: flex-start;
    height: auto;
    padding: 10px;
  }

  .top-bar .welcome,
  .top-bar .UserToken {
    max-width: 100%;
    font-size: 12px;
    white-space: normal; /* se ajusta en varias líneas */
  }

  .top-bar form button,
  .logout-button {
    align-self: flex-end;
    width: 100%;
    margin-top: 5px;
  }
}