.register {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 60px 16px;
  min-height: calc(100vh - 60px);
  box-sizing: border-box;
  background: #f5f7fb;
}

.register-container {
  width: 360px;
  max-width: 100%;
  background: #ffffff;
  padding: 28px;
  border-radius: 8px;
  box-shadow: 0 6px 18px rgba(0,0,0,0.08);
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.register-container h2 {
  text-align: center;
  margin: 0 0 18px 0;
  font-size: 20px;
}

.register-container form {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

.register-container input {
  width: 92%;
  max-width: 320px;
  display: block;
  margin: 0 auto;
  padding: 10px 12px;
  border: 1px solid #d0d7df;
  border-radius: 6px;
  font-size: 14px;
  box-sizing: border-box;
}

.register-container button {
  width: 92%;
  max-width: 320px;
  padding: 10px;
  margin-top: 6px;
  background-color: #007bff;
  border: none;
  border-radius: 6px;
  color: white;
  cursor: pointer;
  font-size: 15px;
  box-sizing: border-box;
}

.register-container button:hover {
  background-color: #0056b3;
}

.error {
  color: red;
  font-size: 14px;
}

.register-message {
  margin-top: 12px;
  text-align: center;
  font-size: 14px;
  color: #00ff2f;
}

@media (max-width: 420px) {
  .register-container {
    padding: 20px;
  }

  .register-container input,
  .register-container button {
    width: 100%;
    max-width: 100%;
  }
}

.back-to-login {
  width: 92%;
  max-width: 320px;
  padding: 10px;
  margin-top: 4px;
  display: block;
  text-align: center;
  background-color: #e4e9f0;
  border-radius: 6px;
  color: #333;
  font-size: 14px;
  text-decoration: none;
  transition: background-color 0.2s ease;
  box-sizing: border-box;
}

.back-to-login:hover {
  background-color: #cfd6df;
}

.checkbox-autologin {
  width: 92%;
  max-width: 320px;
  display: flex;
  align-items: center;
  gap: 8px;
  margin: 6px auto 0 auto;
  font-size: 14px;
  color: #333;
  cursor: pointer;
}

.checkbox-autologin input[type="checkbox"] {
  width: 16px;
  height: 16px;
  cursor: pointer;
  margin: 0;
}
